* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  .keep-app {
    background-image: linear-gradient(141deg,#aae8cd 0%,#1fc8db 51%,#2cb5e8 75%);
    height: 100vh;
    header {
      height: 120px;
      line-height: 120px;
      font-size: 20px;
      font-weight: 600;
      color: rgb(255, 81, 0);
      padding: 0 40px;
      text-align: left;
      .emoji {
        float: right;
      }
    }
    .keep-btn {
      height: 120px;
      .btn {
        content: ' ';
        width: 120px;
        height: 120px;
        background: #6dc728;
        color: #47fff7;
        display: block;
        margin: 0 auto;
        border-radius: 50%;
        text-align: center;
        line-height: 120px;
      }
    }
    .list {
      height: calc(100% - 240px);
      overflow: auto;
      padding: 40px;
      list-style: none;
      .list-item {
        height: 40px;
        line-height: 40px;
        color: #ffeb3b;
        .icon {
          float: right;
        }
      }
    }
  }
}